import InputI0 from '@/components/identification/subComponents/InputI0.vue'
import ButtonI0 from '@/components/identification/subComponents/ButtonI0.vue'
import ButtonI1 from '@/components/identification/subComponents/ButtonI1.vue'
import FooterI from '@/components/identification/subComponents/FooterI.vue'
import Congratulations from '@/components/cards/congratulations/Congratulations'
export default {
  name: 'inscription',
  components: {
    InputI0,
    ButtonI0,
    ButtonI1,
    FooterI,
    Congratulations
  },
  data () {
    return {
      Display: false,
      error:false
    }
  },
  emits: ['connected'],
  methods: {
    cliked (event) {
      if(!this.$refs.insMain.contains(event.target)) this.Display = false
    },
    displaying () {
      this.Display = !this.Display
    },
    submiting (e) {
      e.preventDefault()
      return false
    },
    register () {
      if (this.validation()) {
        const axios = require('axios')
        axios.post(this.$store.state.baseUrl + 'user.php', {
          type:'inscription',
          name: this.$refs.name.message,
          email: this.$refs.email.message,
          password: this.$refs.password.message
        })
          .then((response) => {
            if(response.data != 'notExist') {
              this.$store.commit('connexion',{email:this.$refs.email.message,name:this.$refs.name.message})
              this.$emit('connected')
              this.$refs.congratulations.display()
            }
            else this.error = true
          })
          .catch((error) => {
            console.log(error)
          })
      }
    },
    validation () {
      let regex1 = /^.{2,22}$/
      let regexEmail =  /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
      if (!regex1.test(this.$refs.name.message)) {
        this.$refs.alertG.displaying('nom incorrect')
        return false
      }
      if (!regexEmail.test(this.$refs.email.message)) {
        this.$refs.alertG.displaying('email incorrect')
        return false
      }
      if (!regex1.test(this.$refs.password.message)) {
        this.$refs.alertG.displaying('mot de passe incorrect')
        return false
      }
      if (this.$refs.password.message !== this.$refs.confirmpw.message) {
        this.$refs.alertG.displaying('passwords did not match')
        return false
      }
      return true
    }
  }
}

