import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import { createStore } from 'vuex'
import AlertG from './components/cards/alertG/AlertG'
const store = createStore({
    state () {
      return {
        //baseUrl: 'https://firstdembouz.herokuapp.com/projet/ngomala_backend/',
        //baseUrl: 'http://dictionaryeditor.unaux.com/projet/ngomala_backend/',
        //baseUrl: 'http://localhost:8000/',
        baseUrl: 'https://guide-zdct.onrender.com/',
        user: {
          connected:false,
          email:'',
          name:''
        },
        pubMatch: {// allow to match elements of list with their index ex: 0 = Maison  à louer, 1=Hotel
          type:['Maison à louer','Hotel','Terrain en vente'],
          toiletType:['pot','toilette moderne'],
          bathRoomType:['salle de bain moderne','sale de bain à sceau d\'eau'],
          position: ['Rez de chaussée','Etage 1','Etage 2','Etage 3','Etage 4','Etage 5','Etage 6','Etage 6 +']
        },
        currency: ['FCFA','USD','EURO','INR']
      }
    },
    mutations: {
      connexion(state,playload) {
        state.user.connected = true
        state.user.email = playload.email
        state.user.name = playload.name
      }
    }
  })

const app = createApp(App)
app.use(router)
app.use(store)
app.component('AlertG',AlertG)
app.mount('#app')
