<template>
  <div class="houses" ref="houses" @scroll="showScrollTop">
    <div style="z-index:2" class="housesHead">
        <div @click="$router.push('/guides/'+guideInfos.country+'/'+guideInfos.city)" class="housesImgA">
          <img src="@/assets/arrow-back.svg"/></div>
        <div>{{ guideInfos.name }}</div>
        <div @click="visualize(images)" class="housesImg"><img :src="images.length == 0 ? brl+'profile1.svg' : images[0] "/></div>
    </div>
    <div class="housesBody">
      <div class="houseHead2">
          <div class="houseHead2Img" :style="{backgroundImage:'url('+(images.length == 0 ? brl+'profile.svg' : images[0])+')'}" ></div>
          <div>
              <div>Description</div>
              <span>
                {{guideInfos.description == '' ? 'Aucune' : guideInfos.description}}
              </span>
              <div>Salaire</div>
              <span>
                {{ guideInfos.wage }} {{ guideInfos.currency }} par heure
              </span>
              <div>Infos</div>
              <span style="margin-bottom:30px">
                {{ guideInfos.type }}, {{ guideInfos.country }}, {{ guideInfos.city }}, {{ guideInfos.quater }}
              </span>
          </div>
          <div class="houseHeadRate">
                <StarElt v-for="(star,index) in stars" :key="index" 
                :selected='index < (guideInfos.numberOfReview == 0 ? 0: guideInfos.sumOfReview/guideInfos.numberOfReview) ? true : false'/>
                <img @click="reviewShow" class="houseHeadRPen" src="./img/pencil.svg"/>
          </div>
          <div class="houseLike"><Like @notConnected='$refs.login.displaying()' :infos='reviewInfos' :infos1='guideInfos'/></div>
      </div>
      <div class="housesAbout">Logements</div>
       <div v-show="publications.length !== 0" class="servicesMain">
            <Service :elements='publications' :amount='4' @visualize="(images) => visualize(images)" @contact='contacting' class="landSlider0"/>
            <Service :elements='publications' :amount='3' @visualize="(images) => visualize(images)" @contact='contacting' class="landSlider1"/>
            <Service :elements='publications' :amount='2' @visualize="(images) => visualize(images)" @contact='contacting' class="landSlider2"/>
            <Service :elements='publications' :amount='1' @visualize="(images) => visualize(images)" @contact='contacting' class="landSlider3"/>
      </div>
      <NoResult v-show="publications.length === 0" />
    </div>
    <ScrollTop style="z-index:1"  @click="scrollingTop" ref="scrollTop"/>
    <Visualizer style="z-index:2" ref="visualizer"/>
    <Review @notConnected='$refs.login.displaying()' :Infos='reviewInfos' :infos1='guideInfos' ref="review" style="z-index:2"/>
    <Login @toInscription='displayIns' @connected='getReview' ref='login' style="z-index:2"/>
    <Inscription @connected='getReview' ref='inscription' style="z-index:2"/>
    <Contact ref='contact' :infos='guideInfos' :imgUrl="images.length == 0 ? brl+'profile1.svg' : images[0]" style="z-index:2"/>
    <AlertG ref='alertG' style="z-index:2"/>
    <LLoading1 :visible='loading1' style="z-index:2"/>
  </div>
</template>
<script src="./houses.js">
</script>
<style src="./houses.css">
</style>