export default {
  name: 'lDropDown',
  props: {
    bdColor: {
      type: String,
      default: 'solid 1px silver'
    },
    maxHeight: {
      type: String,
      default: '300%'
    },
    maxWidth: {
      type: String,
      default: '40px'
    },
    datas: Array
  },
  data () {
    return {
      current: this.datas[0],
      vheight: '0%',
      vpaddingTop: '0px',
      vpaddingBottom: '0px',
      isCollapsed: false,
      opacity: 0
    }
  },
  methods: {
    heightCollapse () {
      this.vheight = this.isCollapsed ? '0%' : this.maxHeight
      this.vpaddingTop = this.isCollapsed ? '0px' : '5px'
      this.vpaddingBottom = this.vpaddingTop
      this.isCollapsed = !this.isCollapsed
      this.opacity = this.opacity == 0 ? 1 : 0
    },
    selecting (index) {
      this.current= this.datas[index]
      this.heightCollapse()
    }
  }
}
