import InputI0 from '@/components/inscription/InputI0.vue'
import ButtonI0 from '@/components/inscription/ButtonI0.vue'
import RadioButton from '@/components/inscription/RadioButton.vue'
import DropDownList from '@/components/inscription/DropDownList.vue'
import LLoading1 from '@/components/lLoading1/LLoading1'
import DropDown from '@/components/dropDown/DropDown'
import InputSearch from '@/components/inputSearch/InputSearch'
export default {
  name: 'inscription',
  components: {
    InputI0,
    ButtonI0,
    RadioButton,
    DropDownList,
    LLoading1,
    DropDown,
    InputSearch
  },
  data () {
    return {
      loading:false
    }
  },
  methods: {
    submiting (e) {
      e.preventDefault()
      return false
    },
    save () {
      if (!this.validation()) return
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'inscription.php', {
        type:'ins2',
        country: this.$refs.Pays.current,
        city: this.$refs.villes.current,
        wage: this.$refs.wage.message,
        quater: this.$refs.quater.message,
        contact: this.$refs.contact.message,
        currency: this.$refs.currency.current,
        typeCompte: this.$refs.typeCompte.value
      })
        .then(({data}) => {
          console.log(data)
          this.loading = false
          this.$router.push('/inscription3')
        })
        .catch((error) => {
          this.loading = false
          console.log(error)
        })
    },
    validation () {
      if (this.$refs.Pays.state == 0) {
        this.$refs.alertG.displaying('Veuillez choisir un pays')
        return false
      }
      if (this.$refs.villes.state == 0) {
        this.$refs.alertG.displaying('Veuillez choisir une ville')
        return false
      }
      if (this.$refs.quater.message == '') {
        this.$refs.alertG.displaying('Veuillez entrer le nom du quartier')
        return false
      }
      if (this.$refs.contact.message == '') {
        this.$refs.alertG.displaying('Veuillez entrer le contact')
        return false
      }
      if (this.$refs.typeCompte.value == '') {
        this.$refs.alertG.displaying('Veuillez choisir le type de compte')
        return false
      }
      if (this.$refs.wage.message == '') {
        this.$refs.alertG.displaying('Veuillez entrer un salaire')
        return false
      }
      return true
    },
    stateChange (options) {
      if(options.state == 1) {
        this.$refs.villes.currIndex = options.word.index
      }
      this.$refs.villes.changeState()
    }
  },
  beforeRouteEnter(to, from) {
    if(from.name !='inscription1') return {name:'inscription1'}
  }
}
