<template>
<div class="dashListCont" :style="{boxShadow: infos.id==-1 ? 'rgba(249, 120, 120, 0.15) 0px 2px 8px 7px' : 'rgba(99, 99, 99, 0.2) 0px 2px 8px 0px'}">
          <div class="dashDrop"><div>Type</div>
            <DropDown @selected="changeType" :Current='infos.type'
            style="z-index:1" :datas='$store.state.pubMatch.type' maxWidth='100%' maxHeight="190px"/></div>
          <div class="dashDetail">
            <div class="dashDetInfos">
                <div><div>Note</div><span>{{ infos.rate == '' ? '?' : infos.rate }} étoiles</span></div>
                <div><div>Environement</div><span>{{ infos.environment == '' ? '?' : infos.environment }} étoiles</span></div>
                <div><div>Accessibilité</div><span>{{ infos.accessibility == '' ? '?' : infos.accessibility }} étoiles</span></div>
                <div v-show="infos.type==2"><div>Prix</div><span>{{ infos.price === '' ? '?' : infos.price }} {{currency}}</span></div>
                <!-- just for accomodation and hotel -->
                <div v-show="infos.type!=2"><div>Nombre de pièce</div><span>{{ infos.numberOfRoom === '' ? '?' : infos.numberOfRoom }}</span></div>
                <div v-show="infos.type!=2"><div>Type de toilet</div><span>{{ pubMatch.toiletType[infos.toiletType] }}</span></div>
                <div v-show="infos.type!=2"><div>Type de salle de bain</div><span>{{ pubMatch.bathRoomType[infos.bathRoomType] }}</span></div>
                <div v-show="infos.type!=2"><div>Position</div><span>{{ pubMatch.position[infos.position] }}</span></div>
                <div v-show="infos.type!=2"><div>Veranda</div><span>{{infos.veranda == 1 || infos.veranda ? 'oui' : 'non'}}</span></div>
                <div v-show="infos.type!=2"><div>Jardin</div><span>{{infos.garden == 1 || infos.garden ? 'oui' : 'non'}}</span></div>
                <div v-show="infos.type!=2"><div>Prix par mois</div><span>{{ infos.monthlyPrice === '' ? '?' : infos.monthlyPrice }} {{currency}}</span></div>
                <!-- just for hotel -->
                <div v-show="infos.type==1"><div>Prix par jour</div><span>{{ infos.dailyPrice === '' ? '?' : infos.dailyPrice }} {{currency}}</span></div>
                <div v-show="infos.type==1"><div>Prix par heure</div><span>{{ infos.hourlyPrice === '' ? '?' : infos.hourlyPrice }} {{currency}}</span></div>
                <!-- just for field -->
                <div v-show="infos.type==2"><div>Superficie disponible</div><span>{{ infos.superficies }} m2</span></div>
                <div><div>Description</div><span>{{ infos.description == '' ? 'Aucune' : infos.description }}</span></div>
            </div>
