          <div v-for="(ph,index) in infos.images.slice(0,infos.numberOfImg)" :key="index" :class="ph.modeEdit ? 'dashImagePub':'dashImage1'">
                <ImageUploader
                    :id="'uploader'+ 'dashPub' +index + infos.id"
                    :ref="'imUploader'+ 'dashPub' +index + infos.id"
                    :debug="1"
                    :maxWidth="612"
                    :maxHeight="612"
                    :autoRotate = true
                    :quality = "1"
                    outputFormat="blob"
                    :preview='false'
                    @input="preview($event, 'dashPub' +index + infos.id,ph)"
