                />
                <label v-show="ph.modeEdit" :for="'uploader' + 'dashPub' + index + infos.id" class="custom-file-upload dashLabel">
                    <img src="./img/picture.svg">
                    <div class="dashPlus"><img src="./img/plus.svg"/></div>
                    <span>Ajouter une image</span>
                </label>
                <div v-show="!ph.modeEdit">
                  <img :src='ph.url'/>
