<template>
  <div v-show="Display" class="houseForm">
    <div class="houseFormForm">
        <h3 class="houseFormTitle">Details</h3>
        <div class="houseFormMain">
            <div><div>Note</div><input v-model="infos.rate" placeholder="de 1 à 5" type="number" max=5 min=1/></div>
            <div><div>Environement</div><input v-model="infos.environment" placeholder="de 1 à 5" type="number" max=5 min=1/></div>
            <div><div>Accessibilité</div><input v-model="infos.accessibility" placeholder="de 1 à 5" type="number" max=5 min=1/></div>
            <div v-show="infos.type==2"><div>Prix</div><input v-model="infos.price" placeholder="0" type="number"/></div>
            <!-- just for accomodation and hotel -->
            <div v-show="infos.type!=2"><div>Nombre de pièce</div><input v-model="infos.numberOfRoom" placeholder="100 maximum" type="number" max=100 min=1/></div>
            <div v-show="infos.type!=2">
              <div>Type de toilet</div>
              <select v-model="toiletType">
                <option v-for="(toilet,index) in pubMatch.toiletType" :key="index">
                  {{toilet}}
                </option>
              </select>
            </div>
            <div v-show="infos.type!=2">
              <div>Type de salle de bain</div>
              <select v-model="bathRoomType">
                <option v-for="(bathRoom,index) in pubMatch.bathRoomType" :key="index">
                  {{bathRoom}}
                </option>
              </select>
            </div>
            <div v-show="infos.type!=2">
              <div>position</div>
              <select v-model="position">
                <option v-for="(posi,index) in pubMatch.position" :key="index">
                  {{posi}}
                </option>
              </select>
            </div>
            <div v-show="infos.type!=2" class="houseFormCheck"><div>Veranda</div><input v-model="infos.veranda" type='checkbox'/></div>
            <div v-show="infos.type!=2" class="houseFormCheck"><div>Jardin</div><input v-model="infos.garden" type='checkbox'/></div>
            <div v-show="infos.type!=2"><div>Prix par mois</div><input v-model="infos.monthlyPrice" placeholder="0" type="number"/></div>
            <!-- just for hotel -->
            <div v-show="infos.type==1"><div>Prix par jour (FCFA)</div><input v-model="infos.dailyPrice" placeholder="0" type="number"/></div>
            <div v-show="infos.type==1"><div>Prix par heure (FCFA)</div><input v-model="infos.hourlyPrice" placeholder="0" type="number"/></div>
            <!-- just for field -->
            <div v-show="infos.type==2"><div>Superficie disponible (m2)</div><input v-model="infos.superficies" placeholder="Superficie" type="number"/></div>
            <div><div>Description</div><textarea v-model="infos.description" placeholder="Description"></textarea></div>
        </div>
        <div @click="Display=false" class="houseFormCross"><img src="./img/more.svg"/></div>
        <div @click='save' class="houseFormButton"><span>Enregistrer</span></div>
    </div>
    <AlertG ref='alertG' style="z-index:3"/>
  </div>
</template>
<script src='./houseForm.js'>
</script>
<style src='./houseForm.css'>
</style>