export default {
  name: 'lPublication',
  props: {
    datas: {
      type: Object,
      default: {title:'operum ipsum apsomun',description:'pumom upsum ipsumum aruminum'},
    }
  },
  data () {
    return {
      Height: '400px',
      star_colors: [{ color: '#FDAA07' },{ color: '#FDAA07' },{ color: '#FDAA07' },{ color: '#FDAA07' },{ color: 'transparent' }],
      pubMatch:this.$store.state.pubMatch,
      images:[]
    }
  },
  methods: {
    slideText () {
      this.Height = this.Height == 'auto' ? '400px' : 'auto'
    }
  },
  mounted () {
    let bUrl = this.$store.state.baseUrl + 'images/'
    if(this.datas.img0 != '' ) this.images.push(bUrl+this.datas.img0)
    if(this.datas.img1 != '' ) this.images.push(bUrl+this.datas.img1)
    if(this.datas.img2 != '' ) this.images.push(bUrl+this.datas.img2)
    if(this.datas.img3 != '' ) this.images.push(bUrl+this.datas.img3)
  }
}
