import LPublication from '../lPublication/LPublication'
export default {
  name: 'pubElements',
  props: {
    elements: {
      type: Array,
      default: [1,2,3,4,5,6,7,8]
    },
    amount: {
      type: Number,
      default: 4
    },
    start: {
      type: Number,
      default: 0
    }
  },
  components: {
    LPublication
  },
  methods: {
    ml (index) {
      return index == 0 ? '0%' : '1.6%'
    },
    mr (index) {
      return index == this.amount-1 ? '0%' : '1.6%'
    }
  }
}
