import ButtonNav1 from './ButtonNav1.vue'
import ButtonNav2 from './ButtonNav2.vue'
import MenuButton from './menuButton/MenuButton'
import LDropDown from '@/components/lDropDown/LDropDown'
export default {
  name: 'lNavBar',
  components: {
    ButtonNav1,
    ButtonNav2,
    MenuButton,
    LDropDown
  },
  emits: ['login','register'],
  props: {
    selected: {
        type: String,
        default: 'home'
    }
  },
  data () {
    return {
      langs: [{name:'Fr',image:'fr.svg'},{name:'Eng',image:'uk.svg'}],
      vheight: '0px',
      vopacity: '0',
      vFsize: '0px'
    }
  },
  methods: {
    cross () {
      this.vheight = this.vheight === '0px' ? '315px' : '0px'
      this.vopacity = this.vopacity === '0' ? '1' : '0'
      this.vFsize = this.vFsize === '0px' ? '14px' : '0px'
    }
  },
  computed: {
    DispLogin () {
        return this.$store.state.login.connected ? 'none' : 'block'
    },
    DispMenu () {
        return this.$store.state.login.connected ? 'block' : 'none'
    },
    homeBd () { return this.selected === 'home' ? 'solid 1px rgb(0,81,255)' : 'none' },
    aboutBd () { return this.selected === 'about' ? 'solid 1px rgb(0,81,255)' : 'navButton' },
    servicesBd () { return this.selected === 'services' ? 'solid 1px rgb(0,81,255)' : 'navButton' },
    testimoniesBd () { return this.selected === 'testimonies' ? 'solid 1px rgb(0,81,255)' : 'navButton' }
  }
}
