import Service from '@/components/service/Service'
import Visualizer from './Visualizer'
import ScrollTop from '@/components/scrollTop/ScrollTop'
import StarElt from '@/components/cards/StarElt'
import Review from '@/components/cards/review/Review'
import Contact from '@/components/cards/contact/Contact'
import NoResult from '@/components/cards/noResult/NoResult'
import Like from '@/components/cards/Like'
import Inscription from '@/components/identification/Inscription'
import Login from '@/components/identification/Login'
import LLoading1 from '@/components/lLoading1/LLoading1'
export default {
    components: {
        Service,
        Visualizer,
        ScrollTop,
        StarElt,
        Review,
        Contact,
        NoResult,
        Like,
        Inscription,
        Login,
        LLoading1
    },
    name: 'houses',
    data () {
        return {
            loading1: false,
            id:0,
            guideInfos:{description:'',wage:'0000',type:'Personne',country:'pays',city:'ville',quater:'quater'},
            publications:[],
            images:[],
            brl: this.$store.state.baseUrl,
            reviewInfos:{engaged:'',rate:'',problem:'',liked:false},
            stars:[{selected:true},{selected:true},{selected:true},{selected:false},{selected:false}],
        }
    },
    methods: {
        visualize (images) {
            this.$refs.visualizer.open(images)
        },
        contacting ()  {
            if(this.$store.state.user.connected) this.$refs.contact.displaying()
            else this.$refs.login.displaying()
        },
        scrollingTop () {
            this.$refs.houses.scrollTo({top:0,behavior:'smooth'})
        },
        showScrollTop () {
            //Math.abs(element.scrollHeight - element.clientHeight - element.scrollTop) < 1
            this.$refs.houses.scrollTop > 700 ? this.$refs.scrollTop.show() : this.$refs.scrollTop.hide()
        },
        reviewShow () {
            this.$refs.review.show()
        },
        displayIns () {
            this.$refs.inscription.displaying()
            this.$refs.login.displaying()
        },


        //------- getting data fonctions ---------
        getGuide () {
            this.loading1 = true
            const axios = require('axios')
            //axios.defaults.withCredentials = true
            axios.post(this.$store.state.baseUrl + 'guideClient.php', {
                type:'guide',
                id: this.id
            })
            .then(({data}) => {
                if(data === 'error') {
                    this.$router.push('/choisirVille')
                    return
                }
                this.guideInfos = data
                let bUrl = this.$store.state.baseUrl + 'images/'
                if(data.profilImg0 != '' ) this.images.push(bUrl+data.profilImg0)
                if(data.profilImg1 != '' ) this.images.push(bUrl+data.profilImg1)
                if(data.profilImg2 != '' ) this.images.push(bUrl+data.profilImg2)
                if(data.profilImg3 != '' ) this.images.push(bUrl+data.profilImg3)
                this.loading1 = false
            })
            .catch((error) => {
                this.getGuide()
                console.log(error)
            })
        },
        getPublication () {
            this.loading1 = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'guideClient.php', {
                type: 'publications',
                guide: this.id
            })
            .then(({data}) => {
                this.publications = data
                this.loading1 = false
            })
            .catch((error) => {
                this.getPublication()
                console.log(error)
            })
        },

        getReview () {
            this.loading1 = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'user.php', {
                type: 'getReview',
                id:this.id
            })
            .then(({data}) => {
                if(data != 'none') {
                    this.reviewInfos = data
                }
                this.loading1 = false
            })
            .catch((error) => {
                this.getReview()
                console.log(error)
            })
        }
    },
    async mounted () {
        this.id = this.$route.params.id
        await this.getGuide()
        await this.getPublication()
        if(this.$store.state.user.connected) await this.getReview()
    }
}