import { createWebHistory, createRouter } from 'vue-router'
import Houses from '@/components/houses/Houses'
import Landing from '@/components/landing/Landing'
import GuideList from '@/components/guideList/GuideList'
import Inscription1 from '@/components/inscription/Inscription1'
import Inscription2 from '@/components/inscription/Inscription2'
import Inscription3 from '@/components/inscription/Inscription3'
import RecoverAccount from '@/components/inscription/RecoverAccount'
import Connexion from '@/components/inscription/Connexion'
import Dashboard from '@/components/dashboard/Dashboard'
import ChooseCity from '@/components/chooseCity/ChooseCity'

const routes = [
  {
    path: '/',
    name: 'landing',
    component: Landing
  },
  {
    path: '/guide/:id',
    name: 'guide',
    component: Houses
  },
  {
    path: '/guides/:pays/:ville',
    name: 'guides',
    component: GuideList
  },
  {
    path: '/inscription1',
    name: 'inscription1',
    component: Inscription1
  },
  {
    path: '/inscription2',
    name: 'inscription2',
    component: Inscription2
  },
  {
    path: '/inscription3',
    name: 'inscription3',
    component: Inscription3
  },
  {
    path: '/recupererCompte',
    name: 'recoverAccount',
    component: RecoverAccount
  },
  {
    path: '/connexion',
    name: 'connexion',
    component: Connexion
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    component: Dashboard
  },
  {
    path: '/choisirVille',
    name: 'chooseCity',
    component: ChooseCity
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
