<template>
  <div :style="{borderColor:state==0?'rgb(0, 81, 255)' :'rgb(243, 126, 122)'}" class="inputSearch">
    <div class="inputSearchText">
      <div class="inputSearhTextFirst">
        <input 
        v-show="state == 0 ? true : false"
        :readonly='state == 0 ? false : true'
        @mouseover="isComp=true"
        :id="'inputToSearch'+type + id" 
        v-model="toSearch" type="text" 
        :placeholder="placeholder">
        <div v-show="state == 0 ? false : true" class="inputSearchResult"> {{ toSearch }}</div>
      </div>
    </div>
    <div :class="state == 0 ? 'inputSearchButton' : 'inputSearchButtonA'">
      <button @click="changeState">
        <img class="inputFirstImg" src="./img/loupe.svg"/>
        <img src="./img/exchange.svg"/>
      </button>
    </div>
    <div
      @mouseleave="isComp=false"
      @mouseover="isComp=true"
      v-show="autDisp && isComp && !state" 
      class="inputSearchComple">
      <span @click="selecting(aut)"
          v-for="(aut, index) in autoCompletion" :key="index" >{{ aut.name }}</span>
    </div>
  </div>
</template>
<style src="./inputSearch.css">
</style>
<script src="./inputSearch.js">
</script>
