import InputSearch from '@/components/inputSearch/InputSearch'

export default {
    name: 'chooseCity',
    components: {
        InputSearch
    },
    methods: {
        stateChange (options) {
            if(options.state == 1) {
              this.$refs.villes.currIndex = options.word.index
            }
            this.$refs.villes.changeState()
        },
        validation () {
            if (this.$refs.Pays.state == 0) {
              alert('Veuillez choisir un pays')
              return false
            }
            if (this.$refs.villes.state == 0) {
              alert('Veuillez choisir une ville')
              return false
            }
            return true
        },
        toGuides() {
          if(this.validation()) {
            this.setCookie('countryIndex',this.$refs.villes.currIndex,5)
            this.setCookie('cityName',this.$refs.villes.current,5)
            this.$router.push({name:'guides', params: {pays: this.$refs.Pays.current, ville:this.$refs.villes.current }})
          }
        },

        //-- cookie useful functions ------------
        setCookie(cname, cvalue, exdays) {
          const d = new Date();
          d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
          let expires = "expires="+d.toUTCString();
          document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
        },
        getCookie(cname) {
          let name = cname + "=";
          let ca = document.cookie.split(';');
          for(let i = 0; i < ca.length; i++) {
            let c = ca[i];
            while (c.charAt(0) == ' ') {
              c = c.substring(1);
            }
            if (c.indexOf(name) == 0) {
              return c.substring(name.length, c.length);
            }
          }
          return "";
        }
    },
    mounted () {
      let countryIndex = this.getCookie('countryIndex')
      let cityName = this.getCookie('cityName')
      console.log(countryIndex+"/"+cityName)
      if(countryIndex !== '' && cityName !== '') {
        this.$refs.Pays.setDefault(countryIndex,cityName)
        this.$refs.villes.setDefault(countryIndex,cityName)
      }
    }
}