
export default {
  name: 'houseForm',
  data () {
    return {
      infos: {   id:-1,
                    type:0,
                    rate:1, environment: 1,accessibility:1,numberOfRoom:1,
                    price:'',
                    toiletType:1,
                    bathRoomType:0,
                    position:0,veranda:1,garden:0,
                    monthlyPrice:3000,dailyPrice:3000,
                    hourlyPrice:3000,
                    superficies:3000,
                    description:'description',
                    imgName:'',
                    images:[{file:null,modeEdit:true}],numberOfImg:0},
      Display:false,
      pubMatch:{},
      position:0,
      toiletType:0,
      bathRoomType:0,
      currIndex:-1, // the id of the publication calling the form. -1 for a new publication
    }
  },
  emits: ['save'],
  methods: {
    display (infos,index) {
      this.currIndex = index
      this.infos = {...infos}
      this.infos.garden = this.infos.garden == 1 ? true : false
      this.infos.veranda = this.infos.veranda == 1 ? true : false
      this.position = this.pubMatch.position[this.infos.position],
      this.toiletType = this.pubMatch.toiletType[this.infos.toiletType],
      this.bathRoomType = this.pubMatch.bathRoomType[this.infos.bathRoomType],
      this.Display = true
    },
    save () {
      if (!this.validation()) return
      this.infos.position = this.pubMatch.position.indexOf(this.position)
      this.infos.toiletType = this.pubMatch.toiletType.indexOf(this.toiletType)
      this.infos.bathRoomType = this.pubMatch.bathRoomType.indexOf(this.bathRoomType)
      this.infos.garden = this.infos.garden ? 1 : 0
      this.infos.veranda = this.infos.veranda ? 1 : 0
      this.$emit('save',{infos:this.infos,index:this.currIndex})
      this.Display = false
    },
    validation () {
      // verification
      let regex1 = /[1,2,3,4,5]/
      let regex2 = /^[0-9]{0,2}.$/
      let regexPrix = /^[0-9]{0,10}.$/
      if (!regex1.test(this.infos.rate)) {
        this.$refs.alertG.displaying('la note n\'est pas entre 1 et 5')
        return false
      }
      if (!regex1.test(this.infos.environment)) {
        this.$refs.alertG.displaying('environement n\'est pas entre 1 et 5')
        return false
      }
      if (!regex1.test(this.infos.accessibility)) {
        this.$refs.alertG.displaying('accessibilité n\'est pas entre 1 et 5')
        return false
      }

      //---- type 0 (maison à louer) vérification ----------------------
      if(this.infos.type == 0) {
        if (!regex2.test(this.infos.numberOfRoom)) {
          this.$refs.alertG.displaying('nombre de pièce incorrect')
          return false
        }
        if (!regexPrix.test(this.infos.monthlyPrice)) {
          this.$refs.alertG.displaying('prix par mois incorrect')
          return false
        }
        return true
      }

      //---- type 1 (Hotel) vérification ----------------------
      if(this.infos.type == 1) {
        if (!regex2.test(this.infos.numberOfRoom)) {
          this.$refs.alertG.displaying('nombre de pièce incorrect')
          return false
        }
        if (!regexPrix.test(this.infos.monthlyPrice)) {
          this.$refs.alertG.displaying('prix par mois incorrect')
          return false
        }
        if (!regexPrix.test(this.infos.dailyPrice)) {
          this.$refs.alertG.displaying('prix par jour incorrect')
          return false
        }
        if (!regexPrix.test(this.infos.hourlyPrice)) {
          this.$refs.alertG.displaying('prix par heure incorrect')
          return false
        }
        return true
      }

      //---- type 2 (terrain en vente) vérification ----------------------
      if(this.infos.type == 2) {
        if (!regexPrix.test(this.infos.price)) {
          this.$refs.alertG.displaying('prix incorrect')
          return false
        }
        if (!regexPrix.test(this.infos.superficies)) {
          this.$refs.alertG.displaying('superficie incorrect')
          return false
        }
        return true
      }
    }
  },
  mounted () {
    this.pubMatch = this.$store.state.pubMatch
  }
}