import InputI0 from '@/components/inscription/InputI0.vue'
import ButtonI0 from '@/components/inscription/ButtonI0.vue'
import Infos from '@/components/cards/infos/Infos'
import LLoading1 from '@/components/lLoading1/LLoading1'
export default {
  name: 'inscription1',
  components: {
    InputI0,
    ButtonI0,
    Infos,
    LLoading1,
  },
  data () {
    return {
      loading:false
    }
  },
  methods: {
    save () {
      if (!this.validation()) return
      this.loading = true
      const axios = require('axios')
      //axios.defaults.withCredentials = true
      axios.post(this.$store.state.baseUrl + 'inscription.php', {
        type:'ins1',
        nom: this.$refs.surname.message,
        email: this.$refs.email.message,
        password: this.$refs.password.message
      })
        .then(({data}) => {
          this.loading = false
          if(data=='exist') this.$refs.alertG.displaying('Email déjà enregistrée')
          else this.$refs.infos.displaying(this.$refs.email.message)
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    validateEmail (code) {
      this.loading = true
      const axios = require('axios')
      //axios.defaults.withCredentials = true
      axios.post(this.$store.state.baseUrl + 'inscription.php', {
        type:'validateEmail',
        code: code
      })
        .then(({data}) => {
          this.loading = false
          if(data === 'success') {
            this.$refs.alertG.displaying('Success')
            this.$router.push('/inscription2')
          }
          else this.$refs.alertG.displaying('le code est incorrect')
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    validation () {
      let regexEmail =  /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
      if (this.$refs.surname.message == '' ||
        !regexEmail.test(this.$refs.email.message)) {
        this.$refs.alertG.displaying('Nom ou email incorrect')
        return false
      }
      if (this.$refs.password.message == '') {
        this.$refs.alertG.displaying('Mot de passe incorrect')
        return false
      }
      if (this.$refs.password.message !== this.$refs.confirmpw.message) {
        this.$refs.alertG.displaying('Les mots de passe ne correspondent pas')
        return false
      }
      return true
    }
  }
}
