import InputI0 from '@/components/inscription/InputI0.vue'
import ButtonI0 from '@/components/inscription/ButtonI0.vue'
import ButtonI1 from '@/components/inscription/ButtonI1.vue'
import DropDownList from '@/components/inscription/DropDownList.vue'
import LLoading1 from '@/components/lLoading1/LLoading1'
export default {
  name: 'Connexion',
  components: {
    InputI0,
    ButtonI0,
    ButtonI1,
    DropDownList,
    LLoading1
  },
  data () {
    return {
      loading:false
    }
  },
  methods: {
    submiting (e) {
      e.preventDefault()
      return false
    },
    loggin () {
      if (!this.validation()) return
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'connexion.php', {
        type:'guide',
        email: this.$refs.email.message,
        password: this.$refs.password.message
      })
        .then(({data}) => {
          if(data !== 'notExist') {
            this.$router.push('/dashboard')
          }
          else this.$refs.alertG.displaying('compt inexistant')
          this.loading = false
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    validation () {
      let regex1 = /^.{2,100}$/
      if (!regex1.test(this.$refs.email.message)) {
        this.$refs.alertG.displaying('email incorrect')
        return false
      }
      if (this.$refs.password.message == '') {
        this.$refs.alertG.displaying('mot de passe incorrect')
        return false
      }
      return true
    }
  }
}
