export default {
  name: 'dropDown',
  props: {
    bdColor: {
      type: String,
      default: 'none'
    },
    datas: {
      type: Array,
      default: ['FCFA','USD','EURO','INR']
    },
    maxHeight: {
      type: String,
      default: '800%'
    },
    maxWidth: {
      type: String,
      default: '200px'
    },
    Current: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      current: this.datas[this.Current],
      vheight: '0%',
      vpaddingTop: '0px',
      vpaddingBottom: '0px',
      isCollapsed: false,
      opacity: 0
    }
  },
  emits: ['selected'],
  methods: {
    heightOpen () {
      this.vheight = this.maxHeight
      this.vpaddingTop = '5px'
      this.vpaddingBottom = this.vpaddingTop
      this.isCollapsed = true
      this.opacity = 1
    },
    heightClose () {
      this.vheight = '0%'
      this.vpaddingTop = '0px'
      this.vpaddingBottom = this.vpaddingTop
      this.isCollapsed = false
      this.opacity = 0
    },
    selecting (index) {
      this.current= this.datas[index]
      this.$emit('selected',index)
      this.heightClose()
    }
  },
  watch: {
    Current (newVal) {
      this.current = this.datas[newVal]
    }
  }
}
