import ImageUploader from 'vue-image-upload-resize/src/components/ImageUploader'
import LLoading1 from '@/components/lLoading1/LLoading1'
import Congratulations from '@/components/cards/congratulations/Congratulations'
import ButtonI0 from '@/components/inscription/ButtonI0.vue'

export default {
  name: 'inscription',
  components: {
    ImageUploader,
    LLoading1,
    Congratulations,
    ButtonI0
  },
  data () {
    return {
      loading:false,
      profils: [{file:null,modeEdit:true},{file:null,modeEdit:true}]      
    }
  },
  methods: {
    preview (file,index,image) {
      if (file) {
          image.modeEdit = false
          image.file = file
          document.getElementById('lPbblah1'+index).src = URL.createObjectURL(file)
      }
    },
    send () {

        if(this.profils[0].modeEdit || this.profils[1].modeEdit) {
          this.$refs.alertG.displaying('Veuillez choisir les images')
          return
        }

        this.loading = true
        const axios = require('axios')
        let formData = new FormData()
        formData.append('image0', this.profils[0].file)
        formData.append('image1',this.profils[1].file)
        formData.append('type', 'ins3')
        axios.post(this.$store.state.baseUrl + 'inscription.php', formData, {
            headers: {
            'Content-Type': 'multipart/form-data'
            }
        })
        .then((response) => {
          if (response.data === 'Error') {
            this.loading = false
            this.$refs.alertG.displaying(response.data)
          } else {
            this.loading = false
            this.$refs.congratulations.display()
          }
        })
        .catch((error) => {
          this.loading = false
          console.log(error)
        })
    }
  },
  beforeRouteEnter(to, from) {
    if(from.name !='inscription2') return {name:'inscription1'}
  }
}
