<template>
<div class="textEdit">
    <div class="textEditFirst">{{Title}}</div>
    <span 
        v-show="this.type=='text'"
        class="textEditText"
        :contentEditable="edit" 
        :style="{border: edit ? 'solid 1px rgb(225,225,225)':'none'}"
    :id="'textEditFEl'+name">{{text}}
    </span>
    <input 
        v-show="this.type!='text'"
        class="textEditNumb" :readonly="!edit" type='number' 
        :style="{border: edit ? 'solid 1px silver':'none'}"
        v-model='text1'/>
    <span class="textEditMenu">
        <a v-show="edit" @click="cancel" >Annuler</a>
        <span v-show="edit" @click="save">Enregistrer</span>
        <span v-show="!edit" @click="edit=true">Editer</span>
    </span>
</div>
</template>
<style>
    .textEdit {
        display: flex;
        flex-direction: column;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-left: 15px;
        margin-right: 15px;
        border-bottom: solid 1px rgb(240, 240, 240);
        font-size: 14px;
    }
    .textEditFirst {
    }

    .textEditText {
        font-size: 13px;
        color:rgb(108, 108, 108);
        box-sizing: content-box;
    }
    .textEditNumb {
        display: flex;
        font-family: inherit;
        font-size: 14px;
        color:rgb(108, 108, 108);
    }
    .textEditMenu {
        margin-top: 20px;
        font-size: 12px;
        text-align: right;
        color: rgb(60, 141, 255);
        line-height: 0px;
        padding-bottom: 5px;
    }
    .textEditMenu span {
        cursor: pointer;
    }
    .textEditMenu a {
        margin-right: 10px;
        color: rgb(255, 67, 95);
        cursor: pointer;
    }
</style>
<script>
export default {
  name: 'InputI0',
  props: {
    name: {
        type: String,
        default: '0'
    },
    type: {
        type:String, // number or text
        default: 'text'
    },
    Text: String,
    Title: String
  },
  data () {
    return {
      text:this.Text,
      text1: this.Text,
      edit: false
    }
  },
  emits:['saveBd'],
  methods: {
        save () {
            let regex = /^[0-9]{0,8}.$/
            let t = document.getElementById('textEditFEl'+this.name).innerText
            // verification for the case type is not a number
            if (this.type !== 'number' && t == '') {
                this.cancel()
                return
            }
            // verification for the case type == number
            if (this.type == 'number' && (!regex.test(this.text1))) {
                this.cancel()
                return
            }
            let toSave = this.type == 'number' ? this.text1 : t
            this.$emit('saveBd',toSave)
            //this.text1 = this.text
        },
        cancel () {
            this.edit = false
            document.getElementById('textEditFEl'+this.name).innerText = this.text // in case type = text
            this.text1 = this.text // in case type = number
        }
  },
  watch: {
      Text(newVal) {
            this.text = newVal
            this.text1 = newVal
            this.edit = false
      }
  }
}
</script>
