import ImageUploader from 'vue-image-upload-resize/src/components/ImageUploader'
import DropDown from '@/components/dropDown/DropDown'
import LLoading from '@/components/lLoading/LLoading'

export default {
  name: 'dashPub',
  props: {
    infos: {
      type: Object,
      default: {   
        id:-1,
        type:0,
        rate:'', environment: '',accessibility:'',numberOfRoom:'',
        price:'',
        toiletType:1,
        bathRoomType:0,
        position:0,veranda:1,garden:0,
        monthlyPrice:'',dailyPrice:'',
        hourlyPrice:'',
        superficies:'',
        description:'',
        imgName:'',
        images:[{file:null,modeEdit:true}],numberOfImg:0
      }
    },
    currency: {
      type:String,
      default: 'FCFA'
    }
  },
  data () {
    return {
      pubMatch:this.$store.state.pubMatch,
      infosCopy:'',
      modified: false,
      loading:false
    }
  },
  components: {
    ImageUploader,
    DropDown,
    LLoading
  },
  emits: ['edit','deleteMe'],
  methods: {
    preview (file,index,image) {
      if (file) {
          image.url = URL.createObjectURL(file)
          image.modeEdit = false
          image.modified = true
          image.file = file
      }
      this.modified = true
    },
    edit () {
      this.$emit('edit')
      this.modified = true
    },
    changeType (index) {
      this.infos.type = index
    },
    deleteImg (image) {
      image.modeEdit=true
      image.modified = true
      this.modified = true
    },
    sendImage () {
      const axios = require('axios')
      let i = 0, k = 0
      let formData = new FormData()
      formData.append('aim', 'saveImg')
      formData.append('id', this.infos.id)
      formData.append('imgName', this.infos.imgName)
      let images = this.infos.images
      for(i=0;i<images.length;i++) {
        if(!images[i].modeEdit && images[i].modified) {
          formData.append('image'+i, images[i].file)
          k++
          images[i].modified = false 
        }
        // image to be deleted -------
        else if(images[i].modeEdit && images[i].modified) {
          formData.append('image'+i, 'toDelete')
          k++
        }
      }
      if( k===0 ) return // that means there is no image to be saved or deleted
      this.loading = true
      axios.post(this.$store.state.baseUrl + 'publication.php', formData, {
          headers: {
          'Content-Type': 'multipart/form-data'
          }
      })
      .then(() => {
        //--------- prevent recently saved image to be sent again
        for(i=0;i<images.length;i++) {
          if(!images[i].modeEdit && images[i].modified) {
            images[i].modified = false 
          }
        }
        this.loading = false
        this.$refs.alertG.displaying('succes !')
      })
      .catch((error) => {
        console.log(error)
        this.loading = false  
      })
      },
      save () {
        //---- if publication detail have been edited
      if(this.infos.rate == '') {
        this.$refs.alertG.displaying('Veuillez éditer')
        return
      }
      //--- if there is no image return ---------------------
      let i=0, noImage = true
      for(i=0;i<this.infos.images.length;i++) {
        if(!this.infos.images[i].modeEdit) noImage = false
      }
      if(noImage) {
        this.$refs.alertG.displaying('Veuillez choisir au moins une image')
        return
      }
      //------ now you can send at first the details and secondly the images
      let toSend = {...this.infos,aim:'saveInfos'}
      delete toSend['images']
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'publication.php', toSend)
      .then(({data}) => {
        //--- updating this.infos if it is new pub ---------------
        if(this.infos.id == -1) {
          this.infos.id = data.id
          this.infos.imgName = data.imgName
        }
        //----- updating the old version ---------
        this.infosCopy = JSON.parse(JSON.stringify(this.infos))
        this.modified = false
        this.loading = false
        this.sendImage()
      })
      .catch((error) => {
      console.log(error)
      this.loading = false
      })
    },
    deleteMe () {
      if(this.infos.type == -1) {
        this.$emit('deleteMe')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'publication.php', {
        aim:"delete",
        id:this.infos.id
      })
      .then(({data}) => {
        console.log(data)
        //--- emit to parent----------
        this.$emit('deleteMe')
        this.loading = false
      })
      .catch((error) => {
        console.log(error)
        this.loading = false
      })
    },
    cancel () {
      for(const index in this.infos) {
        if(index != 'images') this.infos[index] = this.infosCopy[index]
      }
      this.infos.images[0] = {...this.infosCopy.images[0]}
      this.infos.images[1] = {...this.infosCopy.images[1]}
      this.infos.images[2] = {...this.infosCopy.images[2]}
      this.infos.images[3] = {...this.infosCopy.images[3]}
    }
  },
  mounted () {
    this.infosCopy = JSON.parse(JSON.stringify(this.infos))
  }
}
