import ImageUploader from 'vue-image-upload-resize/src/components/ImageUploader'
import TextEdit from './TextEdit'
import ScrollTop from '@/components/scrollTop/ScrollTop'
import HouseForm from '@/components/cards/houseForm/HouseForm'
import DashPub from '@/components/cards/dashPub/DashPub'
import DropDown from '@/components/dropDown/DropDown'
import LLoading from '@/components/lLoading/LLoading'
import LLoading1 from '@/components/lLoading1/LLoading1'


export default {
    name: 'dashboard',
    components: {
        ImageUploader,
        TextEdit,
        ScrollTop,
        HouseForm,
        DashPub,
        DropDown,
        LLoading,
        LLoading1
    },
    data () {
        return {
            guideInfos:{description:'description', quater:'quater', contact:'0000000',wage:3000,currency:'FCFA',
            email:'email@gmail.com',country:'pays',city:'ville'},
            profils: [/*{file:null,modeEdit:true,modified:false,url:''}*/],
            publiList:false, // display or not list of publications,
            profilList: false, // display or not list of publication
            description: {
                edit:false,
                text:`Mon nom c'est TAMO MBOBDA et je compte vous faire visiter, la city. Ce quartier est si particulier parce que on doirait qu'il font semblant`
            },
            newPub: {   // new publication model
                id:-1,
                type:0,
                rate:'', environment: '',accessibility:'',numberOfRoom:1,
                price:'',
                toiletType:1,
                bathRoomType:0,
                position:0,veranda:0,garden:0,
                monthlyPrice:'',dailyPrice:'',
                hourlyPrice:'',
                superficies:'',
                description:'',
                imgName:'',
                deleted:0,
                images:[{file:null,modeEdit:true,modified:false,url:''},
                {file:null,modeEdit:true,modified:false,url:''},
                {file:null,modeEdit:true,modified:false,url:''},
                {file:null,modeEdit:true,modified:false,url:''}],
                numberOfImg:1
            },
            publications: [],
            loading: false,
            loading1: true
        }
    },
    methods: {
        preview (file,image) {
            if (file) {
                image.url = URL.createObjectURL(file)
                image.modeEdit = false
                image.modified = true
                image.file = file
            }
        },
        sendImage (index) {
            const axios = require('axios')
            let formData = new FormData()
            formData.append('type','saveImg')
            formData.append('index',index)
            formData.append('image'+index, this.profils[index].file)
            
            this.loading1 = true
            axios.post(this.$store.state.baseUrl + 'guide.php', formData, {
                headers: {
                'Content-Type': 'multipart/form-data'
                }
            })
            .then(({data}) => {
                console.log(data)
                this.profils[index].modified = false
                this.$refs.alertG.displaying('succes !')
                this.loading1 = false
            })
            .catch((error) => {
                console.log(error)
                this.loading1 = false
            })
        },
        deleteImg (index) {
            const axios = require('axios')
            this.loading1 = true
            axios.post(this.$store.state.baseUrl + 'guide.php', {
                type:'deleteImg',
                index:index
            })
            .then(({data}) => {
                console.log(data)
                this.profils[index].modeEdit=true
                this.loading1 = false
                this.$refs.alertG.displaying('succes !')
            })
            .catch((error) => {
                this.loading1 = false
                console.log(error)
            })
        },
        //---- getting profil infos ------

        getGuide () {
            this.loading = true
            let bUrl = this.$store.state.baseUrl + 'images/'
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'guide.php', {
                type:'get'
            })
            .then(({data}) => {
                if(data === 'error' ) {
                    this.$router.push('/connexion')
                    return
                }
                this.guideInfos = data
                //--- setting profil images -----------
                this.profils = [
                    {file:null, modeEdit:data.profilImg0==='' ? true:false, modified:false, url:bUrl+data.profilImg0},
                    {file:null, modeEdit:data.profilImg1==='' ? true:false, modified:false, url:bUrl+data.profilImg1},
                    {file:null, modeEdit:data.profilImg2==='' ? true:false, modified:false, url:bUrl+data.profilImg2},
                    {file:null, modeEdit:data.profilImg3==='' ? true:false, modified:false, url:bUrl+data.profilImg3}
                ]
                this.loading = false
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        isExist (a) {
            return (typeof a === 'undefined') || (a === null) ? false : true
        },
        setGuideInfos (pl/*playload*/) {
            this.loading = true
            let toSend = {
                description: this.isExist(pl.description) ? pl.description : this.guideInfos.description,
                wage: this.isExist(pl.wage) ? pl.wage : this.guideInfos.wage,
                quater: this.isExist(pl.quater) ? pl.quater : this.guideInfos.quater,
                contact: this.isExist(pl.contact) ? pl.contact : this.guideInfos.contact,
                currency: this.isExist(pl.currency) ? pl.currency : this.guideInfos.currency,
                type: 'saveInfos'
            }
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'guide.php', toSend)
            .then((/*{data}*/) => {
                this.guideInfos.description = toSend.description
                this.guideInfos.wage = toSend.wage
                this.guideInfos.quater = toSend.quater
                this.guideInfos.contact = toSend.contact
                //console.log(data)
                this.loading = false
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },

        getPublication () {
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'publication.php', {
                aim: 'getPub'
            })
            .then(({data}) => {
                let bUrl = this.$store.state.baseUrl + 'images/'
                let i =0
                for(i=0;i<data.length;i++){
                    this.publications.push ({
                        ...data[i],
                        images:[
                        {file:null, modeEdit:data[i].img0==='' ? true:false, modified:false, url:bUrl+data[i].img0},
                        {file:null, modeEdit:data[i].img1==='' ? true:false, modified:false, url:bUrl+data[i].img1},
                        {file:null, modeEdit:data[i].img2==='' ? true:false, modified:false, url:bUrl+data[i].img2},
                        {file:null, modeEdit:data[i].img3==='' ? true:false, modified:false, url:bUrl+data[i].img3}
                        ],
                        numberOfImg:1
                    })
                }
                this.loading1 = false
            })
            .catch((error) => {
                console.log(error)
            })
        },



        scrollingTop () {
            this.$refs.dashboard.scrollTo({top:0,behavior:'smooth'})
        },
        showScrollTop () {
            this.$refs.dashboard.scrollTop > 700 ? this.$refs.scrollTop.show() : this.$refs.scrollTop.hide()
        },
        edit (index) {
            this.$refs.houseForm.display(this.publications[index],index)
        },
        saveForm (playload) {
            //console.log(playload)
            //this.publications[playload.index] = {...playload.infos}
            this.publications[playload.index].type = playload.infos.type
            this.publications[playload.index].rate = playload.infos.rate 
            this.publications[playload.index].environment = playload.infos.environment
            this.publications[playload.index].accessibility = playload.infos.accessibility
            this.publications[playload.index].numberOfRoom = playload.infos.numberOfRoom
            this.publications[playload.index].price = playload.infos.price
            this.publications[playload.index].toiletType = playload.infos.toiletType
            this.publications[playload.index].bathRoomType = playload.infos.bathRoomType
            this.publications[playload.index].position = playload.infos.position
            this.publications[playload.index].veranda = playload.infos.veranda
            this.publications[playload.index].garden = playload.infos.garden
            this.publications[playload.index].monthlyPrice = playload.infos.monthlyPrice
            this.publications[playload.index].dailyPrice = playload.infos.dailyPrice
            this.publications[playload.index].hourlyPrice = playload.infos.hourlyPrice
            this.publications[playload.index].superficies = playload.infos.superficies
            this.publications[playload.index].description = playload.infos.description
        },
        addPub () { // add new publication
            this.publiList = true
            if(this.publications.length == 0) {
                this.publications.unshift({...this.newPub})
            }
            else if(this.publications[0].id !=-1){
                this.publications.unshift({...this.newPub})
            }
            else this.$refs.alertG.displaying('Enregistrer la dernière publication')
        },
        toHouses() {
            //console.log(this.guideInfos.id)
            this.$router.push({name: 'guide', params: { id: this.guideInfos.id }})
        }
    },
    async mounted () {
        await this.getGuide()
        await this.getPublication()
    }
}