<template>
  <div class="dashboard" ref="dashboard" @scroll="showScrollTop">
    <div style="z-index:3" class="globalHead">
        <div @click="$router.push('/')"><img src="@/assets/arrow-back.svg"/></div>
        <span>Editer le profil</span>
    </div>
    <div class="dashBody">
      <div class="dashGuideInfos">
        <TextEdit name='userDescript' @saveBd='(desc)=> setGuideInfos({description:desc})' :Text="guideInfos.description" Title="Description"/>
        <TextEdit name='quater' @saveBd='(quater)=> setGuideInfos({quater:quater})' :Text="''+guideInfos.quater" :Title="'Quartier ('+guideInfos.country+', '+guideInfos.city+')'" />
        <TextEdit name='contact' @saveBd='(contact)=> setGuideInfos({contact:contact})' :Text="''+guideInfos.contact" Title="Contact" type='number'/>
      </div>
      <div class="dashGuideInfos">
        <div class="dashDrop"><div style="padding-top:10px">Currency</div>
              <DropDown :Current="$store.state.currency.indexOf(guideInfos.currency)"
              @selected="(index)=> setGuideInfos({currency:$store.state.currency[index]})"
              style="z-index:1" maxWidth='100%' maxHeight="190px"/>
        </div>
        <TextEdit name='wage' @saveBd='(wage)=> setGuideInfos({wage:wage})' :Text="''+guideInfos.wage" :Title="'Salaire par heure'" type='number'/>
        <div class="dashSalary">
          <div class="dashSFirst">{{ guideInfos.email }}</div>
          <span>{{ guideInfos.type }}</span>
          <span @click="toHouses" style="cursor:pointer">Voir le profil et les publicatons</span>
        </div>
      </div>
      <div class="dashList">
        <span @click="profilList=!profilList" :class="profilList ? 'dashListTitleS' : 'dashListTitle'">
          Photos de profil (4) <img src="./img/plus1.svg"/>
        </span>
      </div>
      <div class="dashPublications" style="padding-left:5%;padding-right:5%" 
      :style="{maxHeight:profilList? '13000px' : '0px'}">
          <div v-for="(ph,index) in profils" :key="index" :class="ph.modeEdit ? 'dashImage':'dashImage1'">
                <ImageUploader
                    :id="'uploader'+index + 'profil'"
                    :ref="'imUploader'+index + 'profil'"
                    :debug="1"
                    :maxWidth="612"
                    :maxHeight="612"
                    :autoRotate = true
                    :quality = "1"
                    outputFormat="blob"
                    :preview='false'
                    @input="preview($event,ph)"
                    style="display:none"
                />
                <label v-show="ph.modeEdit" :for="'uploader' + index + 'profil'" class="custom-file-upload dashLabel">
                    <img src="./img/picture.svg">
                    <div class="dashPlus"><img src="./img/plus.svg"/></div>
                    <span>Ajouter profil {{index + 1}}</span>
                </label>
                <div v-show="!ph.modeEdit">
                  <img :src='ph.url' />
                  <div class="dashITopMenu">
                    <label :for="'uploader' + index + 'profil'"><img src="./img/exchange.svg"/></label>
                    <div @click="deleteImg(index)"><img src="./img/trash.svg"/></div>
                  </div>
                  <div v-show="ph.modified" @click="sendImage(index)" class="dashISaved">
                    Enregistrer
                  </div>
                </div>
            </div>
        </div>

      
      <!-- list of publication below -->
      
      <div style="margin-top:20px" class="dashList">
        <span @click="publiList=!publiList" :class="publiList ? 'dashListTitleS' : 'dashListTitle'">
          Publications ({{ publications.length }}) <img src="./img/plus1.svg"/>
        </span>
        <div @click="addPub" class="dashListContAdd"><span>Ajouter</span></div>  
      </div>
      <div class="dashPublications" :style="{maxHeight:publiList? '13000px' : '0px'}">
        <DashPub v-for="(pub,index) in publications" :infos='pub' :key="index" 
        :currency='guideInfos.currency' 
        @edit='edit(index)'
        @deleteMe="publications.splice(index, 1)"
        />  
      </div>
    </div>
    <ScrollTop @click="scrollingTop" ref="scrollTop" style="z-index:3"/>
    <HouseForm @save='saveForm' ref='houseForm' style="z-index:3" />
    <LLoading :visible='loading' style="z-index:3"/>
    <AlertG ref='alertG' style="z-index:3"/>
    <LLoading1 :visible='loading1' style="z-index:3"/>
  </div>
</template>
<script src="./dashboard.js">
</script>
<style src="./dashboard.css">
</style>