<template>
<div class="likeElt">
    <svg class="likeEltImg" @click="save(infos,infos1)" width="800px" height="800px" viewBox="0 0 24 24" fill="red" xmlns="http://www.w3.org/2000/svg">
        <path :fill="active? 'rgb(162, 162, 162)':'none'" stroke="rgb(255,255, 255)" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" d="M6 10C8.28283 8.32595 11.3995 5.54014 13.8271 1.64258C14.1784 1.07865 14.9676 0.967672 15.4374 1.43746L16.0265 2.02648C16.313 2.31302 16.3983 2.74415 16.2424 3.1182L14 8.50003H21.1257C22.3661 8.50003 23.3073 9.61747 23.0966 10.8398L21.2862 21.3398C21.1208 22.2991 20.2888 23 19.3153 23H10L6 20.5" />
        <path :fill="active? 'rgb(162, 162, 162)':'none'" d="M6 8H1V22H6V8Z" stroke="rgb(255,255, 255)" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
    </svg>
    <span>{{ infos1.numberOfLike }}</span>
</div>
</template>
<script>
export default {
    name: 'like',
    props: {
        infos: {
            type: Object
        },
        infos1: {
            type: Object
        }
    },
    emits:['notConnected'],
    data () {
        return {
          active:false,
        }
    },
    methods: {
        save (infos,infos1) {
            if(!this.$store.state.user.connected) {
                this.$emit('notConnected')
                return
            }
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'user.php', {
                type:'like',
                liked: !this.active
            })
            .then(() => {
                infos.liked = !this.active
                this.active=!this.active
                infos1.numberOfLike = this.active ? infos1.numberOfLike+1 : infos1.numberOfLike-1
            })
            .catch((error) => {
                console.log(error)
            })
        }
    },
     watch: {
        infos () {
            this.active = this.infos.liked === 1 ? true : false
        }
    }
}
</script>
<style>
    .likeElt {
        display: flex;justify-content: center;align-items: center;
        flex-direction: column;
        color: white;
        font-weight: bold;
    }
    .likeEltImg {
        width: 22px;
        height:22px;
		cursor: pointer;
    }
</style>