import LLoading from '@/components/lLoading/LLoading'


export default {
  name: 'review',
  props: {
    display:  {
      type: Boolean,
      default: false
    },
    Infos:  {
      type: Object,
      default: {engaged:'',rate:'',problem:'',liked:false}
    },
    infos1:  {
      type: Object
    }
  },
  components: {
    LLoading
  },
  emits:['notConnected'],
  data () {
    return {
      Display: this.display,
      infos: this.Infos,
      loading:false
    }
  },
  methods: {
    close () {
      this.Display = false
    },
    show () {
      this.Display = true
    },
    save () {
      if(!this.$store.state.user.connected) {
        this.$emit('notConnected')
        return
      }
      if (this.validation()) {
        this.loading = true
        const axios = require('axios')
        axios.post(this.$store.state.baseUrl + 'user.php', {
          type:'review',
          engaged: this.infos.engaged,
          rate: this.infos.rate,
          problem: this.infos.problem,
          liked: this.Infos.liked
        })
          .then(({data}) => {
            //console.log(data)
            this.$refs.alertG.displaying('Fait !')
            this.Infos.engaged = this.infos.engaged
            this.Infos.rate = this.infos.rate
            this.Infos.problem = this.infos.problem

            this.infos1.numberOfReview += data.numberOfReviewPlus
            this.infos1.sumOfReview += data.sumOfReviewPlus
            this.loading = false
            this.Display = false
          })
          .catch((error) => {
            console.log(error)
            this.loading = false
          })
      }
    },
    validation () {
      let regex1 = /[0,1,2,3,4,5]/
      let regex2 = /^[0-9]{0,2}.$/
      if (!regex2.test(this.infos.engaged)) {
        this.$refs.alertG.displaying('mauvaise entrée au premier champ')
        return false
      }
      if (!regex1.test(this.infos.rate)) {
        this.$refs.alertG.displaying('note incorrect')
        return false
      }
      return true
    }
  },
  watch: {
    Infos (newVal) {
      this.infos = newVal
    }
  }
}
