import LLoading from '../lLoading1/LLoading1.vue'
import datas from '@/assets/datas.json'
export default {
    name: "InputSearch",
    props: {
        placeholder: String,
        id: {
            type: String,
            default:''
        },
        type: {
            type: String,
            default:''
        }
    },
    components: {
        LLoading
    },
    data() {
        return {
            toSearch: "",
            filters:[],
            loading: false,
            autoCompletion: [],
            datas: ['a','aaO','aaa','aaaa','aaaaa'],
            isComp: true,
            state: 0, // 1 means selected sate 0 means nothing selected,
            currIndex: 0, // the index selected
            current:'', // current value selected
            dontClear:'' // allow to dont clear toSearch when blurred fired
        };
    },
    computed: {
        autDisp () { return this.autoCompletion.length === 0 ? false:true}
    },
    emits:['stateChange'],
    methods: {
        search (word) {
            this.type === 'Ville' ? this.searchCity (word) : this.searchCountry(word)
        },
        searchCity (word) {
            this.autoCompletion = []
            let i = 0,j,k, count = 0
            let cityName = this.removeAccents(word)
            i = this.currIndex
            //console.log(i+" "+datas[i].country_name)
            for(j=0; j<datas[i].states.length; j++){
                if (typeof datas[i].states[j].cities === 'undefined' 
                || datas[i].states[j].cities === null) {
                    continue
                }
                for(k=0; k<datas[i].states[j].cities.length; k++){
                    //console.log(datas[i].states[j].cities[k].city_name)
                    let str = this.removeAccents(datas[i].states[j].cities[k].city_name)
                    if(str.includes(cityName)) {
                        this.autoCompletion.push({name:datas[i].states[j].cities[k].city_name})
                        count++
                    }
                    if (count > 300) break
                }
                if (count > 300) break
            }
        },
        searchCountry (word) {
            this.autoCompletion = []
            let i = 0, count = 0
            let countryName = this.removeAccents(word)
            for(i=0; i<datas.length; i++){
                //console.log(datas[i].country_name)
                let str = this.removeAccents(datas[i].country_name)
                if(str.includes(countryName)) {
                    this.autoCompletion.push({index:i,name:datas[i].country_name})
                    count++
                }
                if (count > 300) break
            }
        },
        removeAccents (str) {
            return str.normalize('NFD').replace(/[\u0300-\u036f]/g, '').toLowerCase()
        },
        selecting (word) {
            this.toSearch = this.type + ' : ' + word.name
            this.current = word.name
            this.autoCompletion = []
            this.state = 1
            this.$emit('stateChange',{state:this.state,word:word})
        },
        changeState () {
            if(this.state == 1) {
                this.state = 0
                this.toSearch = ''
                this.$emit('stateChange',{state:this.state})
            }
            else this.search('')
        },
        setDefault (countryIndex,cityName) {
            if(this.type == 'Pays') {
                this.toSearch = this.type + ' : ' + this.datas[countryIndex].country_name
                this.current = this.datas[countryIndex].country_name
                this.autoCompletion = []
                this.state = 1
            }
            else {// type == Ville
                this.toSearch = this.type + ' : ' + cityName
                this.current = cityName
                this.currIndex = countryIndex
                this.autoCompletion = []
                this.state = 1
            }
        }
    },
    mounted () {
        this.datas = datas
        let k = this
        let input = document.getElementById("inputToSearch"+this.type+this.id)
        input.addEventListener('input',function(event){
            //alert(event.data)
            let word = event.data === null ? '' : event.data
            if (typeof word === 'undefined' || word === null) word = ''
            k.dontClear = true
            input.blur()
            input.focus()
            k.search(k.toSearch)

        })
    }
}